* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212122;
  background-color: azure;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.form {
  text-align: center;
  height: 50px;
  background-color: #00008b;
  margin-top: 0;
  padding: 10px;
  display: block;
}

#search-form {
  font: inherit;
  text-align: left;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
}

#search-form:hover, #search-form:focus {
  opacity: .9;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
}

li {
  list-style: none;
}

.photo-card {
  flex-direction: column;
  justify-content: space-evenly;
  display: none;
}

.image {
  width: 250px;
  height: 200px;
  height: 30vh;
  object-fit: cover;
  display: block;
}

.load-more {
  text-align: left;
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
}

.load-more.is-hidden {
  display: none;
}

.img_card {
  margin-top: 30px;
  margin-right: 30px;
  position: relative;
}

.image:hover, .image:focus {
  opacity: .8;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.photo-card {
  margin: 15px;
  display: flex;
}

.info {
  color: #000;
  border: 1px solid rgba(27, 31, 35, .15);
  border-top: none;
  border-radius: 0 0 4px 4px;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  box-shadow: 0 1px rgba(27, 31, 35, .04), 0 1px rgba(255, 255, 255, .25);
}

.info-item {
  flex-direction: column;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}

.gallery {
  flex-flow: wrap;
  flex-basis: calc(33.3333% - 10px);
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.search-button {
  background-color: #7fffd4;
  border-radius: 5px;
}

.search-button:hover, .search-button:focus {
  opacity: .9;
  cursor: pointer;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
}

